.modalBackground {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.229);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalContainer {
    position: relative;
    width: 70%;
    height: 90vh;
    overflow-x: hidden;
    overflow-y: scroll;
    box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
        0px 5px 8px 0px rgba(0, 0, 0, 0.14), 0px 1px 14px 0px rgba(0, 0, 0, 0.12);
    background: #111;
    border-radius: 8px;
    transition: all 400ms ease-in-out 2s;
    animation: fadeIn 400ms;
}

.modalContainer .title {
    display: block;
    text-align: center;
}

.titleCloseBtn {
    position: fixed;
    margin-left: 0.5rem;
    display: flex;
    justify-content: flex-end;
}

.fa-circle-xmark {
    color: #ffffff;
}

.titleCloseBtn button {
    background-color: transparent;
    border: none;
    font-size: 25px;
    cursor: pointer;
}




@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.5);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

.modal-img {
    width: 100%;
    object-fit: cover;
}

.modal-body {
    color: #ffffff;
}

.modal-body-title {
    width: 65%;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 3rem;
    font-weight: 800;
    max-width: 400px;
    height: 80px;
}

.modal-body-title span {
    white-space: nowrap;
    -webkit-text-size-adjust: none;
}

.modal-body-overview {
    width: 90%;
    line-height: 1.5;
    font-size: 1.12rem;
}

.modal-percentage {
    color: #46d369;
    font-size: 1.2rem;
    display: block;
}