
.navbar {
    width: 100%;
    height: 3.5rem;
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 2rem;
    z-index: 100;
    transition: background-color cubic-bezier(.46, .43, .1, .99) 0.5s;
}

nav img {
    width: 5rem;
    height: 1.4rem;
}

.scroll {
    background-color: #141414;
}

.navbar ul{
    list-style: none;
    display: flex ;
    gap: 1rem;
    color: #ffffff;
}