.banner {
    height: 85vh;
    width: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}

.banner::after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 5rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, .61), #111);
}

.banner-items {
    position: absolute;
    display: flex;
    flex-direction: column;
    bottom: 25rem;
    left: 3rem;
    color: #ffffff;
    font-size: 3rem;
    font-weight: 800;
}

.banner-overview {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-weight: 500;
    font-size: 1rem;
    max-width: 400px;
    height: 80px;
}