#carousel {
    overflow-y: hidden;
    overflow-x: scroll;
    position: relative;
}

.post-icon {
    position: absolute;
    top: 50%;
    border-radius: 50%;
    font-size: 1.2rem;
    padding: 0.3rem 0.5rem;
    color: #000000;
    transform: translateY(-50%);
    z-index: 2;
}

.post-icon:hover {
    background-color: #ffffff;
}

.fa-chevron-left {
    left: 0.7rem;
}

.fa-chevron-right {
    right: 0.7rem;
}

.carousel-items {
    overflow-x: scroll;
}

::-webkit-scrollbar {
    display: none;
}

.post-title {
    color: #ffffff;
}


.posts img {
    width: 18rem;
    height: 10rem;
    border-radius: 2px;
    object-fit: cover;
    transition: transform cubic-bezier(.46, .43, .1, .99) 0.5s;
    /* transition: transform 200ms; */
    transform-style: preserve-3d;
}

.posts img:hover {
    transform: scale(1.05);
}

.modal-img {
    width: 100%;
    height: 390px;
    object-fit: cover;
}